* {
    padding: 0;
    margin: 0;
}


.create_base {
    width: 100%;
    height: 110vh;
    /* background-color: yellow; */

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}





.post_pic_div {
    width: 50%;
    height: 60vh;
    /* background-color: rgb(244, 244, 244); */
    /* background-color: red; */


    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: 1fr 1fr 1fr 1fr 2fe 1fr;


    place-items: center;
    gap: 0%;
}




.confirm,
.select,
.option,
.img-get,
.image-preview,
.create_btn {
    width: 100%;
    height: 10vh;
    /* background-color: pink; */

    display: flex;
    align-items: center;
    justify-content: center;
}



.create_btn {
    width: 50%;
    height: 50%;


    color: black;
    background-color: yellow;
}






.login_error {
    font-size: small;

}






.nav_span_right {
    /* background-color: green; */
    width: 20vw;

}




.nav-img {
    width: 100%;
    height: 100%;
}







/* Responsive */



@media only screen and (max-width:500px) {

    .post_pic_div {
        width: 90%;

    }

    .login_error {
        font-size: x-small;
    }

    .create_btn {
        font-size: xx-small;
    }

    .input_post {
        height: 7%;
    }


    .confirm {
        font-size: 50%;
    }



    .select,
    .option,
    .img-get,
    .image-preview,
    .create_btn {
        font-size: xx-small;
    }


    .img-get,
    .option {
        display: none;
    }





}